class Socket {
	constructor(config) {
		// 配置
		this.Config = Object.assign(
			{
				autoHeartBeat: true,
				heartBeatInterval: 1,
				reconnect: true,
				reonnectInterval: 1
			},
			config || {}
		);
		// 事件对象
		this.event = {};
		// 服务对象
		this.server = {};
		// 连接状态
		this.status = false;
		// 禁止自动连接
		this.nologin = false;
		// 错误标记
		this.error = false;
		// 心跳定时器
		this.t = null;
		// 连接间隔器
		this.ct = null;
		// callback
		this.callback = null;
		// Global
		this.Global = {};
		// StartTimes
		this.StartTimes = 0;
		this.OnError = null;
		this.OnClose = null;
		this.OnOpen = null;
		this.OnSend = null;
		this.OnMessage = null;
	}

	Close(e) {
		this.server.close();
		this.nologin = true;
		this.server = null;
		this.callback = null;
		this.event = null;
		this.status = false;
		this.Global = null;
		this.Config = null;
		this.OnError = null;
		this.OnClose = null;
		this.OnOpen = null;
		clearInterval(this.t);
		clearTimeout(this.ct);
		this.OnClose && this.OnClose(e);
	}

	Start(callback) {
		this.connect();
		// 连接成功回调函数
		this.callback = callback || function() {};
	}

	connect() {
		let index = this.Config.host.indexOf("://");
		let prefix = this.Config.host.slice(0, index);
		let host = this.Config.host.slice(index + 3);
		let port = this.Config.port;
		let path = this.Config.path;

		let proto = "ws://";

		switch (prefix) {
			case "https":
				proto = "wss://";
				port = port || 443;
				break;

			case "http":
				proto = "ws://";
				port = port || 80;
				break;

			case "wss":
				proto = "wss://";
				port = port || 443;
				break;

			case "ws":
				proto = "ws://";
				port = port || 80;
				break;

			default:
				proto = "ws://";
				port = port || 80;
				break;
		}

		// console.log("开始连接服务器", `${proto}${host}:${port}${path || "/"}`);

		// 清除两个定时器
		clearInterval(this.t);
		clearTimeout(this.ct);
		// 连接WS服务器
		this.server = new WebSocket(`${proto}${host}:${port}${path || "/"}`);
		// 连接成功事件
		this.server.onopen = this.onopen.bind(this);
		// 收到消息事件
		this.server.onmessage = this.onmessage.bind(this);
		// 关闭事件
		this.server.onclose = this.onclose.bind(this);
		// 错误事件
		this.server.onerror = this.onerror.bind(this);
		// 心跳函数
		if (this.Config.autoHeartBeat) this.HeartBeat();
	}

	onopen() {
		this.status = true;
		this.error = false;
		this.StartTimes++;
		this.OnOpen && this.OnOpen();
		// 开启成功回调 每次断线重连 都要重新执行的函数
		this.callback.call(this, this);
		// console.log("连接成功");
	}

	onmessage(e) {
		this.OnMessage && this.OnMessage(e);
		// 事件触发器
		try {
			let message = JSON.parse(e.data);

			if (message.event && message.event in this.event) {
				this.event[message.event].call(this, this, message.data, message.event);
			}

			if (this.event["*"]) this.event["*"].call(this, this, message.data, message.event);
		} catch (error) {
			console.log("socket错误:", error);
		}
	}

	onclose(e) {
		this.status = false;
		this.OnClose && this.OnClose(e);
		// 关闭回调:清空定时器 判断是否禁止登录 判断错误回调是否在处理 重连
		clearInterval(this.t);
		if (this.nologin) return false;
		if (this.Config.reconnect) {
			this.ct = setTimeout(() => {
				if (this.error) return false;
				console.log("close");
				this.connect();
			}, 1000 * this.Config.reonnectInterval || 1000);
		}
	}

	onerror(e) {
		this.error = true;
		this.status = false;
		this.OnError && this.OnError(e);
		// 错误回调:清空定时器 判断是否禁止登录 设置错误处理标识 重连
		clearInterval(this.t);
		if (this.nologin) return false;

		if (this.Config.reconnect) {
			this.ct = setTimeout(() => {
				console.log("error");
				this.connect();
			}, 1000 * this.Config.reonnectInterval || 1000);
		}
	}

	HeartBeat() {
		// 心跳检测 发送空字符 目前WEBSOCKET不支持PING 仅支持自动返回PONG帧
		this.t = setInterval(() => {
			try {
				if (this.nologin || this.error || !this.status) return false;
				this.server.send("");
			} catch (error) {
				console.log("心跳错误:", error);
			}
		}, 1000 * this.Config.heartBeatInterval || 5000);
	}

	Send(message) {
		this.OnSend && this.OnSend(message);
		this.server.send(message);
	}

	Emit(event, message) {
		// 发送消息事件
		if (this.nologin || this.error || !this.status) return false;

		try {
			if (!message) {
				this.server.send(JSON.stringify({ event: event }));
				return true;
			}

			let data = null;
			if (typeof message !== "object") {
				data = message;
			} else {
				data = { ...message, ...this.Global };
			}

			this.OnSend && this.OnSend({ event: event, data: data });

			this.server.send(JSON.stringify({ event: event, data: data }));

			return true;
		} catch (error) {
			console.log("发送事件错误:", error);
			return false;
		}
	}

	AddListener(event, callback) {
		// 监听事件
		if (!this.event[event]) this.event[event] = callback;
	}

	RemoveListener(event) {
		// 监听事件
		this.event && delete this.event[event];
	}
}

module.exports = Socket;
